@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css);
@import url(https://fonts.googleapis.com/css?family=Manjari&display=swap);
body {
  font-family: 'Manjari',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #000; }

.checkcontainer {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 25px;
  cursor: pointer;
  font-size: 17px;
  -webkit-user-select: none;
  /* Chrome, Opera, Safari */
  -moz-user-select: none;
  /* Firefox 2+ */
  -ms-user-select: none;
  /* IE 10+ */
  user-select: none;
  /* Standard syntax */ }

/* Hide the browser's default checkbox */
.checkcontainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer; }

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%; }

/* On mouse-over, add a grey background color */
.checkcontainer:hover input ~ .checkmark {
  background-color: #ccc;
  cursor: pointer; }

/* Style the checkmark/indicator */
.checkcontainer .checkmark:after {
  content: "";
  position: absolute;
  left: 9px;
  top: 5px;
  width: 7px;
  height: 12px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg) scale(0);
          transform: rotate(45deg) scale(0);
  -webkit-transition: all ease 0.25s;
  transition: all ease 0.25s;
  overflow: hidden; }

/* When the checkbox is checked, add a blue background */
.checkcontainer input:checked ~ .checkmark {
  background-color: #2196F3; }

/* Show the checkmark when checked */
.checkcontainer input:checked ~ .checkmark:after {
  -webkit-transform: rotate(45deg) scale(1);
          transform: rotate(45deg) scale(1);
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s; }

.list-group-item:last-child {
  border-bottom-right-radius: 1rem;
  border-bottom-left-radius: 1rem; }

.list-group-item:first-child {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem; }

.form-control,
.btn {
  border-radius: 0.75rem; }

.App {
  max-width: 640px;
  margin: 10vh auto;
  padding: 1rem 0;
  background-color: #0d0d0d;
  border-radius: 1.5rem;
  color: #ddd;
  min-height: 80vh; }
  .App .logo {
    margin-bottom: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100px; }
  .App .filters {
    background-color: #f3f7fa; }
  .App .list-group-item {
    background-color: #333;
    flex-basis: 80%; }
    .App .list-group-item .form-check .form-check-input {
      width: 1.5rem;
      height: 1.5rem;
      margin-top: 0;
      left: .75rem; }
    .App .list-group-item label {
      white-space: pre-wrap;
      word-break: break-all;
      max-width: 80%; }
    .App .list-group-item label:hover {
      cursor: text;
      text-decoration: underline !important; }
  .App .list-group-item[disabled] {
    pointer-events: none;
    opacity: 0.5; }
  .App .delete-btn {
    font-size: 2rem;
    padding: 0;
    width: 4rem;
    position: absolute;
    right: -1px;
    top: -1px;
    bottom: 0px;
    height: calc(100% + 2px);
    line-height: 3.5rem;
    border-radius: 0; }
  .App .list-group-item:first-child .delete-btn {
    border-top-right-radius: 1rem; }
  .App .list-group-item:last-child .delete-btn {
    border-bottom-right-radius: 1rem; }

